












import Vue from "vue";
import { mapState } from "vuex";
import { Message } from "@/scripts/chat/message";
import { User } from "@/scripts/chat/user";

export default Vue.extend({
  props: ["msg"],
  computed: {
    ...mapState({
      me: (state: any) => state.chat.user
    }),
    isMe(): boolean {
      const res = this.me.pubKey === this.msg!.pubKey;
      return res;
    }
  }
});
